import base from './base'; // 
import axios from './http'; // httpaxios
import qs from 'qs'; // qs

const article = {
  // 
  articleList () {
    return axios.get(`${base.sq}/topics`);
  },
  // ,
  articleDetail (id, params) {
    return axios.get(`${base.sq}/topic/${id}`, {
      params: params
    });
  },
  // post
  login (params) {
    return axios.post(`${base.sq}/accesstoken`, qs.stringify(params));
  },
  upload(formData){
    return axios.post(`${base.pre}/article`, formData,{
      headers: {'Content-Type': 'false'}
    });
  },
  getArticle(param){
    return axios.get(`${base.pre}/article`, {
      params:{
         id:param.id,
         aid:param.aid
      }
    });
  },
  getArticles(id){
    return axios.get(`${base.pre}/article/all`, {
      params:{
        id:id
      }
    });
  }
  // 
}

export default article;



