import base from './base'; // 
import axios from './http'; // httpaxios
import qs from 'qs'; // qs

const user = {
  register(params){
    return axios.post(`${base.pre}/user`, qs.stringify(params));
  },
  login(params){
    return axios.get(`${base.pre}/user`,{
      params:{
        id:params.id,
        psw:params.psw
      },
    });
  },
  updateUser(formData){
    return axios.post(`${base.pre}/user/update`, formData,{
      headers: {'Content-Type': 'false'}
    });
  },
  getCollections(id){
    return axios.get(`${base.pre}/user/collections`,{
      params:{
        id:id,
      },
    });
  },
  getUser(id){
    return axios.get(`${base.pre}/user/other`,{
      params:{
        id:id
      },
    });
  }
}

export default user;



